</template>

<script>
import http from '@/utils/http.js';
import config from '@/utils/config.js';
import { getCoupleInfo, getPartnerInfo, isBound as checkIsBound, clearCoupleInfo } from '../../utils/couple.js';
import { getCoupleStatus, unbindCouple } from '../../api/couple.js';
import { saveCoupleInfo } from '../../utils/couple.js';
import { updateUserProfile } from '../../api/user.js';

export default {
  data() {
    return {
      // ÂØºËà™Ê†èÁõ∏ÂÖ≥
      statusBarHeight: 0,
      navBarHeight: 44,
      screenWidth: 375,
      userInfo: {
        nickName: '',
        avatarUrl: '',
        displayName: '',
        displayAvatar: ''
      },
      // ‰∏™‰∫∫ËµÑÊñôËÆæÁΩÆÁõ∏ÂÖ≥
      showProfileSettings: false,
      useWechatNickname: true,
      customNickname: '',
      isLoading: false,
      // ÊÉÖ‰æ£ÂÖ≥Á≥ªÁõ∏ÂÖ≥
      isBound: false,
      partnerInfo: null,
      bindTime: '',
      // ÊàêÂ∞±Êï∞ÊçÆ
      achievements: [
        { icon: 'üßÅ', name: 'ÁæéÈ£üÂÆ∂', bgColor: 'rgba(255, 217, 61, 0.2)' },
        { icon: '‚úàÔ∏è', name: 'ÊóÖË°åËÄÖ', bgColor: 'rgba(255, 158, 188, 0.2)' },
        { icon: 'üìÖ', name: 'Á∫™ÂøµÊó•', bgColor: 'rgba(217, 172, 255, 0.2)' }
      ]
    };
  },
  computed: {
    // ËÆ°ÁÆóÂú®‰∏ÄËµ∑ÁöÑÂ§©Êï∞
    daysTogether() {
      if (!this.bindTime) return 0;
      try {
        const bindDate = new Date(this.bindTime);
        const now = new Date();
        const diffTime = now - bindDate;
        const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
        return diffDays > 0 ? diffDays : 1; // Ëá≥Â∞ëÊòæÁ§∫1Â§©
      } catch (e) {
        return 0;
      }
    },
    // ËÆ°ÁÆóÂÆπÂô®È°∂ÈÉ®ÂÜÖËæπË∑ù
    containerPaddingTop() {
      const totalHeightPx = this.statusBarHeight + this.navBarHeight;
      const pxToRpx = 750 / this.screenWidth;
      const totalHeightRpx = totalHeightPx * pxToRpx;
      return totalHeightRpx + 20 + 'rpx';
    }
  },
  
  onLoad() {
    this.getSystemInfo();
    this.loadUserInfo();
    this.loadCoupleInfo();
  },
  onShow() {
    // ÊØèÊ¨°È°µÈù¢ÊòæÁ§∫Êó∂ÈáçÊñ∞Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÂíåÊÉÖ‰æ£‰ø°ÊÅØ
    this.loadUserInfo();
    this.loadCoupleInfo();
  },
  
  methods: {
    // Ëé∑ÂèñÁ≥ªÁªü‰ø°ÊÅØ
    getSystemInfo() {
      // #ifdef MP-WEIXIN
      try {
        // Â∞ùËØï‰ΩøÁî®Êñ∞ API
        const windowInfo = wx.getWindowInfo && wx.getWindowInfo();
        const deviceInfo = wx.getDeviceInfo && wx.getDeviceInfo();
        
        if (windowInfo && deviceInfo) {
          this.statusBarHeight = windowInfo.statusBarHeight || 0;
          this.screenWidth = windowInfo.windowWidth || 375;
        } else {
          // ÈôçÁ∫ßÂà∞Êóß API
          const sysInfo = uni.getSystemInfoSync();
          this.statusBarHeight = sysInfo.statusBarHeight || 0;
          this.screenWidth = sysInfo.windowWidth || 375;
        }
      } catch (e) {
        // Â¶ÇÊûúÊñ∞ API ‰∏çÊîØÊåÅÔºåÈôçÁ∫ßÂà∞Êóß API
        const sysInfo = uni.getSystemInfoSync();
        this.statusBarHeight = sysInfo.statusBarHeight || 0;
        this.screenWidth = sysInfo.windowWidth || 375;
      }
      this.navBarHeight = 54;
      // #endif
      // #ifdef H5
      const sysInfoH5 = uni.getSystemInfoSync();
      this.statusBarHeight = sysInfoH5.statusBarHeight || 0;
      this.screenWidth = sysInfoH5.windowWidth || 375;
      this.navBarHeight = 54;
      // #endif
      // #ifndef MP-WEIXIN || H5
      const sysInfoOther = uni.getSystemInfoSync();
      this.statusBarHeight = sysInfoOther.statusBarHeight || 0;
      this.screenWidth = sysInfoOther.windowWidth || 375;
      this.navBarHeight = 54;
      // #endif
    },
    // Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
    loadUserInfo() {
      try {
        const loginInfo = uni.getStorageSync('login_info');
        if (loginInfo && loginInfo.userInfo) {
          this.userInfo = { ...loginInfo.userInfo };
          // ÂàùÂßãÂåñÊòµÁß∞ËÆæÁΩÆÁä∂ÊÄÅ
          this.useWechatNickname = !this.userInfo.displayName || 
            this.userInfo.displayName === this.userInfo.nickName;
          this.customNickname = this.useWechatNickname ? '' : this.userInfo.displayName;
        }
      } catch (error) {
        console.error('Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•', error);
      }
    },
    
    // ÂàáÊç¢‰∏™‰∫∫ËµÑÊñôËÆæÁΩÆÂ±ïÂºÄ/Êî∂Ëµ∑
    toggleProfileSettings() {
      this.showProfileSettings = !this.showProfileSettings;
    },
    // Âä†ËΩΩÊÉÖ‰æ£‰ø°ÊÅØ
    async loadCoupleInfo() {
      try {
        // ÂÖàÊ£ÄÊü•Êú¨Âú∞
        const localCoupleInfo = getCoupleInfo();
        if (localCoupleInfo && localCoupleInfo.isBound) {
          this.isBound = true;
          this.partnerInfo = localCoupleInfo.partnerInfo || null;
          this.bindTime = localCoupleInfo.bindTime || '';
          
          // ÂêåÊó∂‰ªéÊúçÂä°Âô®ÂêåÊ≠•‰∏ÄÊ¨°Áä∂ÊÄÅ
          try {
            const response = await getCoupleStatus();
            if (response && response.data && response.data.isBound) {
              // Êõ¥Êñ∞Êú¨Âú∞‰ø°ÊÅØ
              saveCoupleInfo({
                isBound: true,
                coupleId: response.data.coupleId,
                partnerId: response.data.partnerInfo?.userId || '',
                partnerInfo: response.data.partnerInfo || {},
                bindTime: response.data.bindTime || '',
                role: response.data.role || ''
              });
              this.partnerInfo = response.data.partnerInfo || {};
              this.bindTime = response.data.bindTime || '';
            } else {
              // ÊúçÂä°Âô®ËøîÂõûÊú™ÁªëÂÆöÔºåÊ∏ÖÈô§Êú¨Âú∞
              this.isBound = false;
              this.partnerInfo = null;
              this.bindTime = '';
            }
          } catch (e) {
            console.error('ÂêåÊ≠•ÁªëÂÆöÁä∂ÊÄÅÂ§±Ë¥•', e);
            // ÂêåÊ≠•Â§±Ë¥•Êó∂‰øùÊåÅÊú¨Âú∞Áä∂ÊÄÅ
          }
          return;
        }
        
        // Êú¨Âú∞Ê≤°ÊúâÔºåÊü•ËØ¢ÊúçÂä°Âô®
        this.isBound = false;
        this.partnerInfo = null;
        try {
          const response = await getCoupleStatus();
          if (response && response.data && response.data.isBound) {
            this.isBound = true;
            this.partnerInfo = response.data.partnerInfo || {};
            this.bindTime = response.data.bindTime || '';
            
            // ‰øùÂ≠òÂà∞Êú¨Âú∞
            saveCoupleInfo({
              isBound: true,
              coupleId: response.data.coupleId,
              partnerId: response.data.partnerInfo?.userId || '',
              partnerInfo: response.data.partnerInfo || {},
              bindTime: response.data.bindTime || '',
              role: response.data.role || ''
            });
          }
        } catch (e) {
          console.error('Êü•ËØ¢ÁªëÂÆöÁä∂ÊÄÅÂ§±Ë¥•', e);
          // Êü•ËØ¢Â§±Ë¥•Êó∂‰ΩøÁî®Êú¨Âú∞Áä∂ÊÄÅ
          this.isBound = checkIsBound();
          if (this.isBound) {
            this.partnerInfo = getPartnerInfo();
            const coupleInfo = getCoupleInfo();
            this.bindTime = coupleInfo ? coupleInfo.bindTime : '';
          }
        }
      } catch (e) {
        console.error('Âä†ËΩΩÊÉÖ‰æ£‰ø°ÊÅØÂ§±Ë¥•', e);
        this.isBound = checkIsBound();
        if (this.isBound) {
          this.partnerInfo = getPartnerInfo();
        }
      }
    },
    // Ë∑≥ËΩ¨Âà∞ÁºñËæëÈ°µÈù¢Ôºà‰∏™‰∫∫ËµÑÊñôÔºâ
    goToEdit() {
      this.showProfileSettings = true;
      // ÊªöÂä®Âà∞Ë¥¶Âè∑‰∏éÂÆâÂÖ®Âå∫Âüü
      setTimeout(() => {
        uni.pageScrollTo({
          selector: '.account-section',
          duration: 300
        });
      }, 100);
    },
    
    // ‰ΩøÁî®ÂæÆ‰ø°Â§¥ÂÉè
    selectWechatAvatar() {
      this.userInfo.displayAvatar = this.userInfo.avatarUrl;
      uni.showToast({
        title: 'Â∑≤ÂàáÊç¢‰∏∫ÂæÆ‰ø°Â§¥ÂÉè',
        icon: 'success',
        duration: 1500
      });
    },
    
    // ‰∏ä‰º†Ëá™ÂÆö‰πâÂ§¥ÂÉè
    uploadCustomAvatar() {
      uni.chooseImage({
        count: 1,
        sizeType: ['compressed'],
        sourceType: ['album', 'camera'],
        success: async (res) => {
          // ‰øùÂ≠òÂéüÂßãË∑ØÂæÑÔºå‰∏çËøõË°å‰ªª‰Ωï‰øÆÊîπ
          const originalFilePath = res.tempFilePaths[0];
          
          if (!originalFilePath) {
            console.error('‚ùå [Â§¥ÂÉèÈÄâÊã©] Êú™Ëé∑ÂèñÂà∞ÂõæÁâáË∑ØÂæÑ');
            uni.showToast({
              title: 'ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•',
              icon: 'none'
            });
            return;
          }
          
          console.log('üì∏ [Â§¥ÂÉèÈÄâÊã©] ÂéüÂßãË∑ØÂæÑ:', originalFilePath);
          
          let loadingShown = false;
          try {
            uni.showLoading({
              title: 'Â§ÑÁêÜÂ§¥ÂÉè‰∏≠...',
              mask: true
            });
            loadingShown = true;
            
            // ÂÖàÂ∞ùËØïÂéãÁº©ÂõæÁâáÔºå‰ΩøÁî®ÂéüÂßãË∑ØÂæÑ
            let imagePath = originalFilePath;
            try {
              console.log('üîÑ [Â§¥ÂÉèÈÄâÊã©] ÂºÄÂßãÂéãÁº©ÂõæÁâáÔºåË∑ØÂæÑ:', originalFilePath);
              const compressedPath = await this.compressImage(originalFilePath);
              
              // Â¶ÇÊûúÂéãÁº©ÊàêÂäü‰∏îËøîÂõû‰∫ÜÊñ∞Ë∑ØÂæÑÔºå‰ΩøÁî®ÂéãÁº©ÂêéÁöÑË∑ØÂæÑ
              if (compressedPath && compressedPath.trim() !== '' && compressedPath !== originalFilePath) {
                console.log('‚úÖ [Â§¥ÂÉèÈÄâÊã©] ÂéãÁº©ÊàêÂäüÔºåÊñ∞Ë∑ØÂæÑ:', compressedPath);
                imagePath = compressedPath;
              } else {
                console.log('‚ÑπÔ∏è [Â§¥ÂÉèÈÄâÊã©] ÂéãÁº©ÂêéË∑ØÂæÑÁõ∏ÂêåÊàñÊó†ÊïàÔºå‰ΩøÁî®ÂéüÂõæ');
                imagePath = originalFilePath;
              }
            } catch (compressError) {
              console.warn('‚ö†Ô∏è [Â§¥ÂÉèÈÄâÊã©] ÂõæÁâáÂéãÁº©Â§±Ë¥•Ôºå‰ΩøÁî®ÂéüÂõæ', compressError);
              imagePath = originalFilePath;
            }
            
            // Â∞ùËØï‰∏ä‰º†Âà∞ÊúçÂä°Âô®
            let avatarUrl = originalFilePath; // ÈªòËÆ§‰ΩøÁî®Êú¨Âú∞ÂõæÁâá
            try {
              console.log('üì§ [Â§¥ÂÉèÈÄâÊã©] ÂºÄÂßã‰∏ä‰º†ÔºåË∑ØÂæÑ:', imagePath);
              
              const uploadResult = await http.upload({
                url: config.API.USER.AVATAR_UPLOAD,
                filePath: imagePath,
                name: 'avatar',
                formData: { type: 'avatar' }
              });
              
              avatarUrl = uploadResult.url || uploadResult.data?.url || originalFilePath;
              console.log('‚úÖ [Â§¥ÂÉèÈÄâÊã©] ‰∏ä‰º†ÊàêÂäüÔºåÊúçÂä°Âô®URL:', avatarUrl);
              
              this.userInfo.displayAvatar = avatarUrl;
              
              // ‰∏ä‰º†ÊàêÂäüÂêéÔºåÁ´ãÂç≥Ë∞ÉÁî®Êõ¥Êñ∞Êé•Âè£‰øùÂ≠òÂà∞Êï∞ÊçÆÂ∫ì
              try {
                const currentNickName = this.useWechatNickname 
                  ? this.userInfo.nickName 
                  : (this.customNickname || this.userInfo.displayName || this.userInfo.nickName);
                await updateUserProfile(currentNickName, avatarUrl);
                console.log('‚úÖ [Â§¥ÂÉèÈÄâÊã©] Â§¥ÂÉèÂ∑≤Êõ¥Êñ∞Âà∞ÂêéÁ´ØÊï∞ÊçÆÂ∫ì');
              } catch (updateError) {
                console.error('‚ùå [Â§¥ÂÉèÈÄâÊã©] Êõ¥Êñ∞Â§¥ÂÉèÂà∞ÂêéÁ´ØÊï∞ÊçÆÂ∫ìÂ§±Ë¥•:', updateError);
                // Âç≥‰ΩøÊõ¥Êñ∞Â§±Ë¥•Ôºå‰πüÊõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®
              }
              
              // Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®
              const loginInfo = uni.getStorageSync('login_info') || {};
              if (loginInfo.userInfo) {
                loginInfo.userInfo.displayAvatar = avatarUrl;
                loginInfo.userInfo.avatarUrl = avatarUrl;
                uni.setStorageSync('login_info', loginInfo);
              }
              
              uni.showToast({
                title: 'Â§¥ÂÉè‰∏ä‰º†ÊàêÂäü',
                icon: 'success',
                duration: 1500
              });
              
            } catch (uploadError) {
              // ‰∏ä‰º†Â§±Ë¥•Ôºå‰ΩøÁî®Êú¨Âú∞ÂõæÁâá
              console.warn('‚ö†Ô∏è [Â§¥ÂÉèÈÄâÊã©] Â§¥ÂÉè‰∏ä‰º†Â§±Ë¥•Ôºå‰ΩøÁî®Êú¨Âú∞ÂõæÁâá', uploadError);
              this.userInfo.displayAvatar = originalFilePath;
              
              // Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®ÔºàÂç≥‰Ωø‰∏ä‰º†Â§±Ë¥•Ôºå‰πü‰øùÂ≠òÊú¨Âú∞ÂõæÁâáË∑ØÂæÑÔºâ
              const loginInfo = uni.getStorageSync('login_info') || {};
              if (loginInfo.userInfo) {
                loginInfo.userInfo.displayAvatar = originalFilePath;
                uni.setStorageSync('login_info', loginInfo);
              }
              
              uni.showToast({
                title: 'Â§¥ÂÉèÂ∑≤ÈÄâÊã©ÔºàÊú™‰∏ä‰º†Ôºâ',
                icon: 'none',
                duration: 1500
              });
            }
            
          } catch (error) {
            console.error('‚ùå [Â§¥ÂÉèÈÄâÊã©] Â§ÑÁêÜÂ§¥ÂÉèÂ§±Ë¥•', error);
            uni.showToast({
              title: 'Â§¥ÂÉèÂ§ÑÁêÜÂ§±Ë¥•',
              icon: 'none'
            });
          } finally {
            if (loadingShown) {
              uni.hideLoading();
            }
          }
        },
        fail: (err) => {
          if (err && err.errMsg && !err.errMsg.includes('cancel')) {
            console.error('ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•', err);
            uni.showToast({
              title: 'ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•',
              icon: 'none'
            });
          }
        }
      });
    },
    
    // ÂéãÁº©ÂõæÁâá
    compressImage(tempFilePath) {
      return new Promise((resolve, reject) => {
        uni.compressImage({
          src: tempFilePath,
          quality: 80,
          success: (res) => {
            resolve(res.tempFilePath);
          },
          fail: (error) => {
            console.warn('ÂõæÁâáÂéãÁº©Â§±Ë¥•Ôºå‰ΩøÁî®ÂéüÂõæ', error);
            resolve(tempFilePath);
          }
        });
      });
    },
    
    // ÂàáÊç¢ÊòØÂê¶‰ΩøÁî®ÂæÆ‰ø°ÊòµÁß∞
    toggleUseWechatNickname() {
      this.useWechatNickname = !this.useWechatNickname;
      if (this.useWechatNickname) {
        this.customNickname = '';
      }
    },
    
    // ‰øùÂ≠ò‰∏™‰∫∫ËµÑÊñô
    async saveProfile() {
      // È™åËØÅËá™ÂÆö‰πâÊòµÁß∞
      if (!this.useWechatNickname && !this.customNickname.trim()) {
        uni.showToast({
          title: 'ËØ∑ËæìÂÖ•Ëá™ÂÆö‰πâÊòµÁß∞',
          icon: 'none'
        });
        return;
      }
      
      this.isLoading = true;
      
      try {
        // ÊûÑÂª∫ÊúÄÁªàÁî®Êà∑‰ø°ÊÅØ
        const displayName = this.useWechatNickname 
          ? this.userInfo.nickName 
          : this.customNickname.trim();
          
        const displayAvatar = this.userInfo.displayAvatar || this.userInfo.avatarUrl;
        
        // Ë∞ÉÁî®ÂêéÁ´ØAPIÊõ¥Êñ∞Áî®Êà∑ËµÑÊñô
        try {
          await updateUserProfile(displayName, displayAvatar);
          console.log('‚úÖ Áî®Êà∑ËµÑÊñôÂ∑≤Êõ¥Êñ∞Âà∞ÂêéÁ´Ø');
        } catch (apiError) {
          console.error('‚ùå Êõ¥Êñ∞Áî®Êà∑ËµÑÊñôÂà∞ÂêéÁ´ØÂ§±Ë¥•:', apiError);
          // Âç≥‰ΩøÂêéÁ´ØÊõ¥Êñ∞Â§±Ë¥•Ôºå‰πüÊõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®ÔºàÈôçÁ∫ßÂ§ÑÁêÜÔºâ
          uni.showToast({
            title: 'ÂêéÁ´ØÊõ¥Êñ∞Â§±Ë¥•ÔºåÂ∑≤‰øùÂ≠òÂà∞Êú¨Âú∞',
            icon: 'none',
            duration: 2000
          });
        }
        
        // Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®
        const loginInfo = uni.getStorageSync('login_info') || {};
        loginInfo.userInfo = {
          ...loginInfo.userInfo,
          displayName,
          displayAvatar,
          nickName: displayName, // ÂêåÊó∂Êõ¥Êñ∞nickNameÂ≠óÊÆµÔºåÁ°Æ‰øùÂêéÁ´ØÂíåÊú¨Âú∞‰∏ÄËá¥
          avatarUrl: displayAvatar, // ÂêåÊó∂Êõ¥Êñ∞avatarUrlÂ≠óÊÆµ
          originalNickName: this.userInfo.nickName,
          originalAvatarUrl: this.userInfo.avatarUrl
        };
        
        uni.setStorageSync('login_info', loginInfo);
        
        // Êõ¥Êñ∞ÂΩìÂâçÁî®Êà∑‰ø°ÊÅØ
        this.userInfo = { ...loginInfo.userInfo };
        
        uni.showToast({
          title: '‰øùÂ≠òÊàêÂäü',
          icon: 'success',
          duration: 1500
        });
        
      } catch (error) {
        console.error('‰øùÂ≠òÂ§±Ë¥•', error);
        uni.showToast({
          title: '‰øùÂ≠òÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        });
      } finally {
        this.isLoading = false;
      }
    },
    
    // Â§ÑÁêÜËÆæÁΩÆÈ°πÁÇπÂáª
    handleSetting(key) {
      const settingMap = {
        notification: 'ÈÄöÁü•ËÆæÁΩÆ',
        privacy: 'ÈöêÁßÅËÆæÁΩÆ',
        sync: '‰∫ëÂêåÊ≠•'
      };
      uni.showToast({
        title: settingMap[key] + 'ÔºàÂæÖÂºÄÂèëÔºâ',
        icon: 'none'
      });
    },
    
    // Ëß£ÁªëÂÖ≥Á≥ª
    async handleUnbind() {
      uni.showModal({
        title: 'Á°ÆËÆ§Ëß£Áªë',
        content: 'Ëß£Èô§ÂÖ≥Á≥ªÂêéÔºåÂèåÊñπÂ∞ÜÊó†Ê≥ïÂÖ±‰∫´Êï∞ÊçÆ„ÄÇÁ°ÆÂÆöË¶ÅËß£Èô§ÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            try {
              uni.showLoading({ title: 'Ëß£Áªë‰∏≠...' });
              await unbindCouple();
              uni.hideLoading();
              
              // Ê∏ÖÈô§Êú¨Âú∞‰ø°ÊÅØ
              clearCoupleInfo();
              
              uni.showToast({
                title: 'Â∑≤Ëß£Èô§ÂÖ≥Á≥ª', 
                icon: 'success' 
              });
              
              // Êõ¥Êñ∞È°µÈù¢Áä∂ÊÄÅ
              this.isBound = false;
              this.partnerInfo = null;
              this.bindTime = '';
              
              // Âª∂ËøüÂà∑Êñ∞È°µÈù¢
              setTimeout(() => {
                this.loadCoupleInfo();
              }, 1500);
            } catch (error) {
              uni.hideLoading();
              console.error('Ëß£ÁªëÂ§±Ë¥•', error);
              uni.showToast({ 
                title: error.message || 'Ëß£ÁªëÂ§±Ë¥•ÔºåËØ∑ÈáçËØï', 
                icon: 'none' 
              });
            }
          }
        }
      });
    }
  }
};
</script>

